import React, {useState} from 'react';
import { useHistory } from "react-router-dom";
import Header from "../headers/GeneralHeader";
import * as constant from '../components/Constants'
import usePost from "../hooks/usePost";
import DisplayAlert from '../components/DisplayAlert';
import ReactGA from 'react-ga4';

function ResetRequest() {
  let path = window.location.hash.split('#')[1]
  ReactGA.set({ page: path});
  ReactGA.pageview(path);
  //ReactGA.pageview(window.location.pathname + window.location.hash);
  return(
    <div className="container">
      <div className="row">
        <div className="col-sm-2"></div>
        <div className="col-sm-8 chatcolor">
            <Header />
            <br />
            <ResetArea />
        </div>
        <div className="col-sm-2"></div>
      </div>
    </div>
  );
}

export default ResetRequest;

function ResetArea() {
  const [email, setEmail] = useState("");
  const [btnValue, setBtnValue] = useState("Reset Password");
  const [pwdMessage,setPwdMessage] = useState("");
  let history = useHistory();

  function handleSubmit(e) {
    console.log("handleSubmit");
    e.preventDefault();
    let message = '';
    message = <ChangePwd email={email} />
    //message = "A password reset link has been emailed to you. Please click on the link to reset your password"
    setPwdMessage(message);
    setBtnValue("Home")
    setEmail("")
  }
  function handleEmail(e) {
    setEmail(e.target.value);
  }
  function handleClick(e) {
    if(e.target.value === 'Home'){
      sessionStorage.removeItem('loginmessage');
      const topic = sessionStorage.getItem('topic')
      const subtopic = sessionStorage.getItem('subtopic')

      history.push({
          pathname:'/oq/'+topic+'/'+subtopic
      });
    }
  }
  return(
    <div className="pwdreset">
    <form className="loginform" onSubmit={handleSubmit}>
      <label className="pwdtext"> Enter your email id: </label>
      <input type="text" name="email" className="pwdtextbox" placeholder="email id"
      value={email} onChange={handleEmail} required/><br/>
      <label className="pwdmessage">{pwdMessage}</label>
      <input type="submit" value={btnValue} className="pwdbtn" onClick={handleClick}/>
      </form>
    </div>
  );
}

function ChangePwd(props) {
  const url = constant.resetLinkURL;
  let message = '';
  const topic = sessionStorage.getItem('topic')
  const subtopic = sessionStorage.getItem('subtopic')
  const dataText = { "email": props.email, "topic":topic, "subtopic":subtopic }
  const fetchResponse = usePost(url, dataText, {isLoading: true, data: null, error: null});
  if (fetchResponse.error){
    return <DisplayAlert message={fetchResponse.error} />
  }
  else if ( fetchResponse.isLoading) {
    return 'Loading...';
  }
  const response = fetchResponse.data;
  console.log(response);
  let token = response['link'];
  let basename=window.location.href.split('#')[0]
  let link = basename+'#/reset?reset_key='+token
  const serviceID = 'default_service';
  const templateId = 'template_fwfqjsu';
  sendFeedback(serviceID,templateId, {link: link, reply_to: props.email});
  message = "A password reset link has been emailed to you. Please click on the link to reset your password"
  /*if (response['status'] === 'success'){
        message = constant.pwdResetSuccessMsg;
  }
  else{
     message = constant.pwdResetFailedMsg;
  }*/
  return(`${message}`);
}

function sendFeedback (serviceID,templateId, variables) {
  console.log("sendFeedback");
 window.emailjs.send(
   serviceID, templateId,
   variables
   ).then(res => {
     console.log('Email successfully sent!')
   })
   .catch(err => console.error('Oh well, you failed. Here some thoughts on the error that occured:', err))
 }
