import React, {useState} from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import DisplayTopic from '../screens/DisplayTopic';
import ChatBot from '../screens/ChatBot';
import ChatBotOQ from '../oneQuestion/ChatBotOQ';
import UserStats from '../screens/UserStats'
import Responses from '../screens/Responses'
import ResponsesOQ from '../oneQuestion/ResponsesOQ';
import Insights from '../screens/Insights'
import Login from "../screens/Login";
import UserLogin from "../screens/UserLogin";
import Feedback from "../screens/Feedback";
import FeedbackOQ from '../oneQuestion/FeedbackOQ';
import About from "../screens/About";
import AboutOQ from "../oneQuestion/AboutOQ";
import PasswordReset from './PasswordReset';
import ResetRequest from './ResetRequest';
import Page404 from './Page404';
import AdminScreen from "../screens/AdminScreen"
import SessionSelectionScreen from "../screens/SessionSelectionScreen"
import Error from './Error';
import FeedbackThanks from './FeedbackThanks';
import FeedbackThanksOQ from '../oneQuestion/FeedbackThanksOQ';
import Explanation from "../screens/Explanations";
import LoginOQ from '../oneQuestion/LoginOQ';

function Routes(){
    return(
      <Router  basename={`${process.env.PUBLIC_URL}/`}>
      <Switch>
        <Route path="/" exact render={() => <Login/>} />
        <Route path="/topics" exact render={() => <DisplayTopic/>} />
        <Route path="/reset" exact render={() => <PasswordReset/>} />
        <Route path="/resetreq" exact render={() => <ResetRequest/>} />
        <Route path="/chat/:topic/:subtopic" render={() => <ChatBot/>} />
        <Route path="/chatoq/:topic/:subtopic" render={() => <ChatBotOQ/>} />
        <Route path="/oq/:topic/:subtopic" render={() => <LoginOQ/>} />
        <Route path="/user_stats/:topic/:subtopic" render={() => <UserStats/>} />
        <Route path="/view_responses/:topic/:subtopic" render={() => <Responses/>} />
        <Route path="/view_responses_oq/:topic/:subtopic" render={() => <ResponsesOQ/>} />
        <Route path="/explanation/:topic/:subtopic" render={() => <Explanation/>} />
        <Route path="/feedback" render={() => <Feedback/>} />
        <Route path="/feedbackoq" render={() => <FeedbackOQ/>} />
        <Route path="/about" render={() => <About/>} />
        <Route path="/aboutoq" render={() => <AboutOQ/>} />
        <Route path="/admin" exact render={() => <AdminScreen/>} />
        <Route path="/admin/:topic/:subtopic" exact render={() => <SessionSelectionScreen/>} />
        <Route path="/analytics/:topic/:subtopic" render={() =>
                  <Insights />} />
        <Route path="/:topic/:subtopic" render={() => <UserLogin/>} />
        <Route path="/error" render={() => <Error/>} />
        <Route path="/thanks" exact render={() => <FeedbackThanks/>} />
        <Route path="/thanksoq" exact render={() => <FeedbackThanksOQ/>} />

        <Route render={() => <Page404/>} />
      </Switch>
      </Router>
    );
}

export default Routes;
