import React from "react";
import ReactWordcloud from 'react-wordcloud';
import { Resizable } from "re-resizable";
import useGetAttempted from "../hooks/useGetAttempted";

function ShowWordCloud(props) {
  let answers = [];
  if(sessionStorage.getItem('answers')){
    answers = JSON.parse(sessionStorage.getItem('answers'));
  }
  console.log(answers);
  if(props.data.length > 0){
    const words = props.data;
    const options = {
      content: {
        allowNumbers: false,
        maxWords: 20,
        stemmer: null,
        stopwordsInput: '',
        },
      fontSizes: [40, 70],
      rotations: 3,
      rotationAngles: [0, 0],
      padding: 3,
      backgroundColor:"#ffffff"
    }
    const callbacks = {
      getWordColor: (word) => ( answers.includes(word.text) ? "#1e77b4" : "#d62628")
    }
    const resizeStyle = {
      background: "#ede2d5",
      width:'90%',
      height: '100%',
      marginLeft: '5%',
      marginRight: '5%',
    }
    return (
      <ReactWordcloud className="wordcloud" callbacks={callbacks} options={options} words={words} style={resizeStyle}/>
    );
  }
  else {
    return <div></div>
  }
}

export default ShowWordCloud;
