//admin interface
import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { Tabs, Tab, Content } from "../components/Tab";
import Header from "./KwykHeaderOQ";
import '../static/css/header.css';
import '../static/css/stats.css';
import useFetch from "../hooks/useFetch";
import * as constant from '../components/Constants';
import DisplayAlert from '../components/DisplayAlert';
import ShowWordCloud from './WordCloudOQ'
import * as utils from '../utils/jsutils';
import TopicHeader from "../headers/TopicHeader";
import Collapse from 'react-bootstrap/Collapse';
import ReactGA from 'react-ga4';
import * as getTopicSubTopic from '../utils/getTopicFromURL';

function ResponsesOQ() {
  let path = window.location.hash.split('#')[1]

  ReactGA.set({ page: path});
  ReactGA.pageview(path);
//  ReactGA.pageview(window.location.pathname + window.location.hash);
  let history = useHistory();
  return <ShowResponsePage />
}

function ShowResponsePage() {
  getTopicSubTopic.getTopic()
  const topic = sessionStorage.getItem('topic');
  const subtopic = sessionStorage.getItem('subtopic');
  const username = sessionStorage.getItem('username');
  const useremail = sessionStorage.getItem('useremail');
  return (
    <div className="container">
      <div className="row">
        <div className="col-md-2"></div>
        <div className="col-md-8">
            <Header username={username}/>
            <ShowResponses topic={topic} subtopic={subtopic}/>
        </div>
        <div className="col-md-2"></div>
      </div>
    </div>
  );
}

export default ResponsesOQ;

function ShowResponses(props){
  const useremail = sessionStorage.getItem('useremail');
    const url = constant.kwykURL+"user_stats_custom/"+useremail+"/"+props.topic+"/"+props.subtopic;
  const fetchResponse = useFetch(url, {isLoading: true, data: null, error: null});
  let history = useHistory();
  if (fetchResponse.error){
      return <DisplayAlert message={fetchResponse.error} />
  }
  else if ( fetchResponse.isLoading) {
      return 'Loading...';
  }
  const data = fetchResponse.data
  console.log(data);
  let oqanswer;
  //const data = JSON.parse(sessionStorage.getItem('responsesData'));
  //const topicUserWords = JSON.parse(sessionStorage.getItem('userResponses'));
  const topicWordsResponses = data["topic_answers"];
  const userAnswers = topicWordsResponses["user_answers"][0][useremail];
  const topicWords = topicWordsResponses["topic_words"];
  const topicTopWords = topicWordsResponses["top_words"];

  if(sessionStorage.getItem('usertype') == 'user'){
    console.log("userAnswers=", userAnswers);
    oqanswer = Object.values(userAnswers)[0];
    let answers = oqanswer.split(',');
    answers = answers.map(function (el) {
          el = el.toLowerCase();
          return el.trim();
        });
    sessionStorage.setItem('answers',JSON.stringify(answers));
  }
  topicWords.sort();
  const wordResponses = topicWords.map((word,index)=>{
    return <DisplayWordResponses word={word} topicTopWords={topicTopWords}  key={index}/>
  });
  function handleClick() {
    const topic = sessionStorage.getItem('topic');
    const subtopic = sessionStorage.getItem('subtopic');
    sessionStorage.clear();
    history.push({
      pathname:`/oq/${topic}/${subtopic}`
    });
  }
  function handleFeedback() {
      history.push({
        pathname:`/feedbackoq`
      });
    }
  return(
    <div >
      <TopicHeader topic={props.topic} subtopic={props.subtopic}/>
      <div className= "responses-area">
        <WordCloudHeader />
        <ul className="chatcolor">
          {wordResponses}
        </ul>
        <div className='row topmargin chatcolor'>
          <div className="col-lg-3 chatcolor"></div>
          <div className="col-lg-3 chatcolor">
            <button className="exitbutton" onClick={handleFeedback}>Feedback</button>
          </div>
          <div className="col-lg-3 chatcolor">
            <button className="exitbutton" onClick={handleClick}>Exit</button>
          </div>
          <div className="col-lg-3 chatcolor"></div>
        </div>
      </div>
    </div>
  );
}


function DisplayWordResponses(props){
  //const userWords = props.topicUserWords[props.word];
  const topWords = props.topicTopWords[props.word];
  const size = topWords.length > 20 ? 20 : topWords.length
  return(
    <div className="word-response">
    <label className="topicnameoq">{utils.convertToCamelCase(props.word)}</label>
    <ShowWordCloud data={topWords.slice(0, size)} />
    </div>
  );
}

function DisplayWord(props){
  let words = ""
  if(props.words){
    words = props.words.split(',');
  }
  return(
    <div>
    {words ?
        <div className="white">{words.map((word,index) => <div className="white" key={index}>
            <label className="word">{word}</label>
        </div>)}</div>
        : null}
    </div>
  );
}

function WordCloudHeader() {
  return(
    <div className='wcheader'>
      1. Items marked in Blue are your answers. <br/>
      2. Items marked in red are answers from others <br/>
      3. The font size is an indicator of popular answers
    </div>
  );

}
